// External font imports
@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@400;500;700&family=Space+Mono:wght@400;700&display=swap');

// Define fonts for body and headers
$body-font: 'Space Mono', monospace;
$headers-font: 'Raleway', sans-serif;

// Bootstrap override
body {
  font-family: $body-font;
}

h1, h2, h3, h4, h5, h6 {
  font-family: $headers-font;
  font-weight: 700;
}
